





















































































import { State as loginState } from '@/store/module/login';
import { ElForm } from 'element-ui/types/form.js';
import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';

export interface LoginForm {
  username: string;
  password: string;
}

@Component
export default class Login extends Vue {
  @State('loginState') private loginState!: loginState;
  @Action('login/LOGIN') private LOGIN: any;

  private loginBackground = require('@/assets/img/one.png');
  private pwdType: string = 'password';
  private isShowPwd: boolean = true;
  private iseye: boolean = false;
  private loading: boolean = false;
  private loginForm?: LoginForm = {
    username: '',
    password: '',
  };
  private loginRules = {
    username: [
      { required: true, trigger: 'blur', message: '请输入用户名' },
    ],
    password: [{ required: true, trigger: 'blur', message: '请输入密码' }],
  };


  private showPwd() {
    if (this.pwdType === 'password') {
      this.pwdType = '';
      this.isShowPwd = false;
      this.iseye = true;
    } else {
      this.pwdType = 'password';
      this.isShowPwd = true;
      this.iseye = false;
    }
  }

  private handleLogin() {
    (this.$refs.loginForm as ElForm).validate((valid) => {
      if (valid) {
        this.loading = true;
        this.LOGIN(this.loginForm).then((res: any) => {
          localStorage.setItem('access_token', res.data);
          location.href = '/';
        }).catch(() => {
          this.loading = false;
        });

      } else {
        return false;
      }
    });
  }


