import { HomeService } from '@/service';
import * as types from '@/store/mutation-types';
import _ from 'lodash';
import { ActionTree, Commit, MutationTree } from 'vuex';

export interface State {
  bookList: any[];
  loading: boolean
}

const initState: State = {
  bookList: [],
  loading: false,
};

// getters
const getters = {
  bookList: (state: { bookList: any; }) => {
    return state.bookList;
  },
};

// mutations
const mutations: MutationTree<State> = {
  [types.SET_BOOK_LIST](state: State, data: any): void {
    state.bookList = data;
    state.loading = !state.loading;
  },
  [types.SET_LOADING](state: State): void {
    state.loading = !state.loading;
  },
};

// actions
const actions: ActionTree<State, any> = {
  async getQueryBookList(context: { commit: Commit; state: State }, params: any) {
    context.commit((types.SET_LOADING));
    const res = await HomeService.getQueryBookList(params);
    // @ts-ignore
    if (res.code === 200) {
      const list = _.cloneDeep(context.state.bookList);
      // @ts-ignore
      const arr = _.uniqBy(_.concat(list, res.data), 'isbn');
      context.commit((types.SET_BOOK_LIST), arr);
    } else {
      context.commit((types.SET_LOADING));
    }
  },
};

export default {
  namespaced: true,
  state: initState,
  getters,
  actions,
  mutations,
};
